import React, { useState, useEffect } from 'react';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';

import { initializeApp } from 'firebase/app';
import { getAuth, signOut } from 'firebase/auth';
import { getAnalytics } from 'firebase/analytics';

const firebaseConfig = {
  apiKey: "AIzaSyC5qgMh1cC8RsfXHafcnJu5BioJRIm1hdw",
  authDomain: "my-website-8f892.firebaseapp.com",
  projectId: "my-website-8f892",
  storageBucket: "my-website-8f892.appspot.com",
  messagingSenderId: "91947991336",
  appId: "1:91947991336:web:663163e19d2c8d5d81901d",
  measurementId: "G-B74BY197TF"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);
const auth = getAuth(app);

const App = () => {
  const [isSignedIn, setIsSignedIn] = useState(false);

  // Firebase UI configuration
  const uiConfig = {
    signInFlow: 'popup',
    signInOptions: [
      // Add your desired sign-in options here
      // Example: Google Sign-In and Email/Password
      auth.GoogleAuthProvider.PROVIDER_ID,
      auth.EmailAuthProvider.PROVIDER_ID,
    ],
    callbacks: {
      signInSuccessWithAuthResult: () => false,
    },
  };

  // Event handler for when the user signs in
  const onSignIn = () => {
    setIsSignedIn(true);
  };

  useEffect(() => {
    const unregisterAuthObserver = auth.onAuthStateChanged((user) => {
      setIsSignedIn(!!user);
    });

    return () => unregisterAuthObserver();
  }, []);

  return (
    <div>
      {isSignedIn ? (
        <div>
          <h1>Welcome, {auth.currentUser.displayName}!</h1>
          <button onClick={() => signOut(auth)}>Sign Out</button>
        </div>
      ) : (
        <StyledFirebaseAuth
          uiConfig={uiConfig}
          firebaseAuth={auth}
          onSuccess={onSignIn}
        />
      )}
    </div>
  );
};

export default App;
